import React, {Component} from "react"
import MyNavLink from "../../Components/MyNavLink";
import {Menu, Button} from 'antd';
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
} from '@ant-design/icons';
import './index.css'

const data_basic = [
    ["jsx", "JSX"],
    ["component", ""],
    ["state-props-refs", "state-props-refs"],
    ["event", ""],
    ["life-cycle", ""],
    ["create-react-app", "create-react-app"],
    ["controlled", ""],
    ["component-combination", ""]
]

const data_more = [
    ["pubsub", "PubSub"],
    ["code-splitting", ""],
    ["accessibility", ""],
    ["context", "Context "],
    ["higher-order-function", ""],
    ["strict-mode", ""],
    ["error-boundaries", ""],
    ["forwarding-refs", "refs "],
    ["higher-order-component", ""],
    ["hook", "Hook"]
]

const router = [
    ["router", "router"]
]

const redux = [
    ["redux", 'Redux'],
    ["react-redux", "React-Redux"]
]

export default class ReactLeftBar extends Component {
    state = {
        collapsed: false,
    };

    toggleCollapsed = () => {
        this.setState({
            collapsed: !this.state.collapsed,
        });
    };

    render() {
        return (
            <div style={{width: 256}}>
                <Button type="primary" onClick={this.toggleCollapsed} style={{marginBottom: 16}}>
                    {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
                </Button>
                <Menu inlineCollapsed={this.state.collapsed}>
                    <div className="leftbar-item-title">
                        React 
                    </div>
                    {
                        data_basic.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/react/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                    <div className="leftbar-item-title">
                        React 
                    </div>
                    {
                        data_more.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/react/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                    <div className="leftbar-item-title">
                        React-Router
                    </div>
                    {
                        router.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/react/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                    <div className="leftbar-item-title">
                        Redux
                    </div>
                    {
                        redux.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/react/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                </Menu>
            </div>
        );
    }
}