import React, {Component} from "react"
import MyMarkdownTitle from "../MyMarkdownTitle";
import readme from '../../note/README.md'
import hc_readme from '../../note/CSS/README.md'
import hc_unit from '../../note/CSS/.md'
import hc_dis from '../../note/CSS/display.md'
import hc_pos from '../../note/CSS/position.md'
import hc_float from '../../note/CSS/float.md'
import hc_box from '../../note/CSS/.md'
import hc_hide from '../../note/CSS/.md'
import hc_center from '../../note/CSS/.md'
import hc_flex from '../../note/CSS/.md'
import hc_bfc from '../../note/CSS/BFC.md'
import js_readme from '../../note/JavaScript/README.md'
import js_inhtml from '../../note/JavaScript/1-HTMLJavaScript.md'
import js_datatype from '../../note/JavaScript/2-.md'
import js_func from '../../note/JavaScript/3-.md'
import js_obj_basic from '../../note/JavaScript/4--.md'
