import React, {Component} from "react"
import MyNavLink from "../../Components/MyNavLink";
import {Menu, Button} from 'antd';
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
} from '@ant-design/icons';

const data = [
    ["inhtml", "HTMLJavaScript"],
    ["datatype", ""],
    ["function", ""],
    ["object_basic", "-"],
    ["object_prototype", "-"],
    ["object_extend", "-"],
    ["object_class", "-"],
    ["dom", "DOM"],
    ["bom", "BOM"],
    ["ajax", "AJAX"],
    ["variable", ""],
    ["event", ""],
    ["array", "-Array"],
    ["date", "-Date"],
    ["json", "-JSON"],
    ["regexp", "-RegExp"],
    ["string", "-String"],
    ["transform", "ES6 "],
    ["var_let_const", "var-let-const"],
    ["destructure", ""],
    ["extend", ""],
    ["deepclone", ""],
    ["iterator", ""],
    ["async_basic", "-"],
    ["async_promise", "-Promise"],
    ["async_generator", "-Generator"],
    ["async_func", "-async"],
]

export default class JSLeftBar extends Component {
    state = {
        collapsed: false,
    };

    toggleCollapsed = () => {
        this.setState({
            collapsed: !this.state.collapsed,
        });
    };

    render() {
        return (
            <div style={{width: 256}}>
                <Button type="primary" onClick={this.toggleCollapsed} style={{marginBottom: 16}}>
                    {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
                </Button>
                <Menu inlineCollapsed={this.state.collapsed}>
                    {
                        data.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/javascript/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                </Menu>
            </div>
        );
    }
}